.comment_container{
    margin-bottom: 75px;
    margin-top: 75px;

}
.comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    height: 45px;
    margin-top: 10px;
}

.comment .MuiAvatar-circle{
    border: 2px solid #B69A5C;
    width: 40px!important;
    height: 40px!important;
}

.comment_details{
    flex: 1;
    font-size: 13px;
}

.comment_details > h5 {
    color: grey;
    margin: 0px;

}

.comment_card_avatar{
    margin-right: 10px;
}

.comment_likes {
    display: flex;
    font-size: 11px;
    margin-top: 5px;
    color: grey;

}

.comment_likes > p {

    margin-right: 5px;
}

.comment_comment{
    font-weight: 600;
    margin-top: 5px;
}

.likes_icon  {
    color: #B69A5C;
}


.card h5, p{
    margin: 0px;
    margin: 0px;
}

.comment_input{
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;
    background-color: white;
}

.comment_input_field{
    flex: 1;
    padding: 10px;
    border: none;
}

.comment_input_button{
    border: none;
    padding: 20px;
    background-color: white;
    font-weight: bolder;
    color: #B69A5C
}

