.profile-page p {
  margin: 0px;
}

.profile-page #profile-info #profile-pic img {
  height: 100px;
  margin-right: auto;
  margin-left: auto;
  display: block;
}

.profile-page #profile-level #profile-trophy img {
  height: 70px;
}

.profile-page #profile-info #profile-username span {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */
  letter-spacing: 0.15px;
  color: #000000;
  text-align: center;
}

.profile-page #profile-level #profile-username span {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 200% */
  letter-spacing: 0.15px;
  color: #a6a6a6;
}

.profile-page #profile-info #profile-username {
  text-align: center;
}

.profile-page #slider-container {
  text-align: center;
  padding-top: 24px;
  padding-right: 30px;
  padding-left: 30px;
  padding-bottom: 24px;
}

.profile-page #slider-container #title {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  /* identical to box height, or 171% */
  letter-spacing: 0.15px;
  color: #000000;
}

.profile-page #slider-container {
  border-bottom: 1px solid lightgrey;
}

.profile-page #slider-container .MuiSlider-root {
  color: #b69a5c;
}

.profile-page #slider-container .MuiSlider-rail {
  height: 5px;
}

.profile-page #slider-container .MuiSlider-track {
  height: 5px;
}

.profile-page #slider-container .MuiSlider-thumb {
  margin-top: -4px;
}

.profile-page #tiles {
  margin-top: 10px;
}

.profile-page #tiles .tile .item {
  border: 1px solid #b69a5c;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  padding: 30px;
}

.profile-page #tiles .tile .icon img {
  margin-right: auto;
  margin-left: auto;
  display: block;
  width: 60px;
  height: 60px;
}

.profile-page #tiles .tile .title {
  text-align: center;
}

/* Challanges Section */
.profile-page #challanges {
  margin-top: 10px;
  padding-bottom: 16px;
}

.profile-page #challanges .challange {
  box-shadow: 0px 1px 1px -1px grey;
  margin-bottom: 2px;
}

.profile-page #challanges .challange .MuiCardContent-root {
  padding-top: 0px;
}

/* Friends Section */
.profile-page #friends {
  margin-top: 10px;
  padding-bottom: 16px;
}

.profile-page #friends .friend {
  box-shadow: 0px 1px 1px -1px grey;
  margin-bottom: 2px;
}

.profile-page #friends .friend .MuiCardContent-root {
  padding-top: 0px;
}

.profile-page #friends .friend .MuiCardContent-root .unfriend-button {
  background: transparent !important;
  color: #000 !important;
  padding: 5px;
  text-transform: none;
  border: 1px solid #b69a5c;
  border-radius: 13px;
}

/* Cards Section */
.profile-page #cards {
  margin-top: 10px;
  padding-bottom: 16px;
}

.profile-page #cards .card {
  margin-top: 12px;
  box-shadow: 0px 2px 1px -1px rgba(182, 154, 92, 1.2),
    0px 1px 1px 0px rgba(182, 154, 92, 1.14),
    0px 1px 3px 0px rgba(182, 154, 92, 1.12);
}

.profile-page #cards .card .MuiCardContent-root {
  padding: 36px;
}

.profile-page #cards .card .MuiCardContent-root h2 {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  /* font-size: 9px; */
  /* line-height: 20px; */
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
  mix-blend-mode: normal;
}
