 .header{
     display: flex;
     justify-content: space-between;
     align-items:  center;
     border-bottom: 1px solid #A6A6A6 ;
     position: fixed;
     top: 0;
     width: 100%;
     margin-left: auto;
     margin-right: auto;
     left: 0;
     right: 0;
     z-index: 1;
    background-color: white;
     
 }

 .header_trophy > img{
    height: 5vh;
    padding: 12px;
    display: flex;
 }

 .header_icon{
     padding: 12px;
     color: #B69A5C
 }

