
.card_options{
    display: flex;
    justify-content: center;
    padding: 20px;
    margin-top: 60px;
}


.card_options .MuiButton-containedPrimary {
    color: #fff;
    background-color: #B69A5C!important;
    margin: 5px!important;
    border-radius: 25px!important;
    width: 100%!important;
    font-size: 10px!important;
    max-width: 100PX;
    /* text-transform: capitalize!important; */


}

.card_options .MuiButton-outlinedPrimary {
    color: #B69A5C!important;
    border: 1px solid #B69A5C!important;
    margin: 5px!important;
    border-radius: 25px!important;
    width: 100%!important;
    font-size: 10px!important;
    max-width: 100PX;
    /* text-transform: capitalize!important; */


}

.card_container{
    /* display: flex; */
    display: grid;
    place-items: center;
    /* justify-content: center; */
    margin-top: 2vh;
    scroll-snap-type: y mandatory;
}

.card{
    position: relative;
    background-color: white;
    width: 300px;
    padding: 20px;
    max-width: 85vw;
    height: 60vh;
    border: 1px solid #888888;
    background-size: cover;
    background-position: center;
    box-shadow: 0px 0px 5px 1px #888888;
    margin-bottom: 50px;
}
.swipe{
    /* position: absolute; */
    position: relative;
    /* scroll-snap-align: start; */
}

.card_text {
    text-align: center;
    display: inline;
    margin: 0px;
    color: #A6A6A6;
}
.card_text > h5{
 margin: 0px;
}


.card_avatar_container{
    display: flex;
    justify-content: center;
    margin-top: -46px;
}

.cards .MuiAvatar-circle{
    border: 2px solid #B69A5C;
    width: 50px!important;
    height: 50px!important;
}

.card_content{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 10px;
}

.card_interactions{
    position: absolute;
    bottom: 0px;
    display: flex;
    justify-content: space-evenly;
    border-top: 1px solid #A6A6A6 ;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    padding: 20px;
}

.card_icon  {
    color: #B69A5C;
}

.card_interactions > a {

    text-decoration: none;
    color: inherit;

}

.card .MuiIconButton-root {
    font-size: 12px!important;
    padding: 0px!important;
}

